.book {
  place-self: center;
  text-align: center;
  transform: translateX(200%);
  animation: header-in;
  animation-duration: 1.5s;
  animation-timing-function: ease-in-out;
  animation-delay: 100ms;
  animation-fill-mode: forwards;
}
@media (min-width: 40em) {
  .book {
    padding: 1.25rem;
  }
}
@media (min-width: 71.875em) {
  .book {
    padding: 1rem 0.4375rem 1rem 1rem;
    text-align: left;
  }
}
.book h1 {
  font-weight: 700;
  font-size: 2.5rem;
  color: var(--vry-dk-violet);
}
.book p {
  line-height: 1.8;
  font-size: 1rem;
  color: var(--dk-gr-violet);
}
.book > *::selection {
  color: var(--lt-magenta);
  background-color: var(--vry-dk-violet);
}
.book > *::-moz-selection {
  color: var(--lt-magenta);
  background-color: var(--vry-dk-violet);
}